import { _decorator, Component, Node, Label, System, assetManager, AssetManager, SpriteAtlas, Prefab, instantiate, resources, SpriteFrame } from 'cc';
import { GameRole } from '../../common/GameRole';
const { ccclass, property } = _decorator;

@ccclass('BZDSceneMgt')
export class BZDSceneMgt extends Component {

    @property(Label)
    public roomId: Label
    @property(Label)
    public gamecount: Label

    @property(Label)
    seatName: Label[] = [];
    @property(Label)
    seatTotalPoints: Label[] = [];
    
    @property(Label)
    seatJuPoints: Label[] = []
    
    @property(Label)
    seatPanPoints: Label[] = []
    
    @property(Node)
    readyOk: Node[] = []
    
    @property(Node)
    readyBtn: Node
    
    @property(Node)
    cancelReadyBtn: Node
    
    @property(Node)
    startBtn: Node

    private gameBundle: AssetManager.Bundle
    private pokerAtlas: SpriteFrame[];
    private pokerPrefab: Prefab;

    
    start() {
        this.init();
        console.log("怡怡最美~");
        console.log("最爱怡怡~");
    }

    private init(){
        globalThis._eventtarget.on("requestRoomInfo", this.onRequestRoomInfo, this);
        globalThis._eventtarget.on("dealCards", this.onDealCards, this);
    }

    update(deltaTime: number) {
        
    }

    public onRequestRoomInfo(data){
        console.log("onrequestroomInfo: ",data);
        let roomInfo = data.roomInfo;
        this.roomId.string = roomInfo.roomId;
        this.gamecount.string = roomInfo.currentNumbers + "/" + roomInfo.gameNumbers + "局 " + roomInfo.scoreNumbers + "分";
        // if(roomInfo.readyNumbers == 4 && globalThis._userInfo.user_id == roomInfo.createUserId)this.startBtn.active = true;
        // else this.startBtn.active = false;
        this.updateAllSeats(roomInfo.gameRoles);
    }

    onDealCards(data){
        console.log("获得牌型：",data);
        this.gameBundle = assetManager.getBundle("resources");
        resources.loadDir("image/games/bazhadan/card", SpriteFrame,null, (err, spriteFrames)=>{
            if(err)console.log("load atlas err: ", err);
            else {
                this.pokerAtlas = spriteFrames;
                console.log("this.atlas: ", this.pokerAtlas);
            }
        })
        resources.load("image/games/bazhadan/PokerView", Prefab, (err, prefab: Prefab)=>{
            if(err)console.log("load prefab err: ", err);
            else {
                console.log("prefab:",prefab);
                let poker = instantiate(prefab);
                this.node.addChild(poker);
                this.pokerPrefab = prefab;}
                console.log("this.prefab: ",this.pokerPrefab);
        })
    }

    updateAllSeats(users: GameRole[]){
        // console.log("获取到所有座位信息：",users);
        let cur = 0;

        //渲染本座位
        for(let i = 0; i < 4; i++){
            if(users[i] == null){
                continue;
            }
            if(users[i].userId == globalThis._userInfo.user_id){
                cur = i;
                this.seatName[0].string = users[i].userName;
                this.seatTotalPoints[0].string = "总分:" + users[i].totalPoints;
                this.seatJuPoints[0].string = "局:" + users[i].juPoints;
                this.seatPanPoints[0].string = "盘:" + users[i].panPoints;
                let ready:boolean = (users[i].isReady == 1);
                this.readyOk[0].active = ready;
                this.readyBtn.active = !ready;
                this.cancelReadyBtn.active = ready;
            }
        }
        globalThis._userInfo.room_seat = cur;
        //渲染其他座位
        for(let i = 1; i < 4; i++){
            if(++cur >= 4)cur -= 4;
            if(users[cur] == null)continue;
            this.seatName[i].string = users[cur].userName;
            this.seatTotalPoints[i].string = "总分:" + users[cur].totalPoints;
            this.seatJuPoints[i].string = "局:" + users[cur].juPoints;
            this.seatPanPoints[i].string = "盘:" + users[cur].panPoints;
            this.readyOk[i].active = (users[cur].isReady == 1);
        }
    }

    onReadyBtnClicked(){
        globalThis._BZDClient.sendMessage("userReady",{seat:globalThis._userInfo.room_seat, isReady: 1, roomId:globalThis._userInfo.room_id})
    }

    onCancelReadyBtnClicked(){
        globalThis._BZDClient.sendMessage("userReady",{seat:globalThis._userInfo.room_seat, isReady: -1, roomId:globalThis._userInfo.room_id})
    }

    onStartBtnClicked(){
        this.startBtn.active = false;
        this.readyBtn.active = false;
        this.cancelReadyBtn.active = false;
        for(let i = 0; i < 4; i++){
            this.readyOk[i].active = false;
        }
        globalThis._BZDClient.sendMessage("dealCards",{});
    }
}

